export default {
  data() {
    return {
      avtiveIndex: 0,
      datas: "",
      uid: "",
      currentPage: 1,
      limit: 10,
      total: 0,
      type: 1,
      tableData: []
    };
  },
  created() {
    this.initdata({
      page: this.currentPage,
      limit: this.limit,
      type: this.type
    });
  },
  methods: {
    //赚钱排行
    makeMoney() {
      this.activehtpp(0, 1);
    },

    //历史排行
    exchange() {
      this.activehtpp(1, 2);
    },

    //历史在线
    online() {
      this.activehtpp(2, 3);
    },

    //查找
    search() {
      if (this.uid === "") {
        this.uid = 0;
      }else if(this.datas === ''){
        this.datas = 0
      }
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        type: this.type,
        uid: this.uid,
        datas: this.datas
      });
      this.uid = "";
      this.datas = "";
    
    },

    //页容量变化
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        type: this.type
      });
    },

    //页码变化
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        type: this.type
      });
    },

    activehtpp(avtiveIndex, type) {
      this.avtiveIndex = avtiveIndex;
      this.type = type;
      this.initdata({
        page: this.currentPage,
        limit: this.limit,
        type: this.type
      });
    },

    async initdata(params) {
      let { data } = await this.$http.HallFunConfig.GetManageHistory(params);
      // let fres = this.formateData(data.data);
      this.tableData = data.data;
      this.total = data.total;
      // console.log(data);
    }
  }
};



